@keyframes SuperButton_blink__WVCCC {
    0% {
        left: -130%;
    }
    100% {
        left: 130%;
    }
}

.SuperButton_default__2vf1J {
    background: #bbffbb;
    margin-bottom: 10px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 8px;
    cursor: pointer;
}

.SuperButton_red__12zn9 {
    background: red;
}


.SuperButton_button__3mVpS {
    position: relative;
    margin: 4px;
    overflow: hidden;
    cursor: pointer;
}

.SuperButton_button__3mVpS::after {
    display: block;
    content: "";
    position: absolute;
    left: -130%;
    top: 0;
    height: 100%;
    width: 20%;

    transform: skew(45deg);
    background-color: #ffffff;
    opacity: 0.7;
    z-index: 14;
}

.SuperButton_button__3mVpS:hover::after {
    animation: SuperButton_blink__WVCCC 0.35s ease;
}

.SuperButton_button__3mVpS:hover::before {
    display: block;
    content: "";
    position: absolute;

    left: -100vw;
    top: 0;
    height: 100vh;
    width: 300vw;
    background-color: #ffffff;
    opacity: 0.2;
    z-index: 7;
}

.SuperButton_button__3mVpS:active {
   background: #005500;

}

.SuperButton_button__3mVpS:disabled {
    color: darkgrey;
    cursor: auto;
}

.SuperButton_button__3mVpS:disabled::after {
    z-index: -1;
}

.SuperButton_button__3mVpS:disabled::before {
    z-index: -1;
}
