@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
    font-family: 'Roboto', sans-serif;
}

label {
    font-size: .7rem;
}

input.form-control, input.form-control:focus {
    background: #F0F0FF;
}

.error {
    position: absolute;
    color: red;
    font-size: .6rem;
}

.App {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}